<template>
  <div class="login-box">
    <h2 style="font-size: 30px;">鬼斯通个人学习文档</h2>
    <form>
      <div class="user-box">
        <input type="text" v-model="rulerFrom.username">
        <label>Username</label>
      </div>
      <div class="user-box">
        <input type="password" v-model="rulerFrom.password">
        <label>Password</label>
      </div>
      <div style="width: 100%; text-align: center;">
        <a href="#" @click="submitgo">
          <span></span>
          <span></span>
          <span></span>
          <span></span>
          Submit
        </a>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rulerFrom: {
        username: 'admin',
        password: '123456'
      }
    };
  },
  methods: {
    submitgo() {
      if (
        this.rulerFrom.username == 'admin' &&
        this.rulerFrom.password == '123456'
      ) {
        this.$message({
          message: '登入成功',
          type: 'success'
        });
        setTimeout(() => {
          localStorage.setItem('token', '登入');
          localStorage.setItem('rule', 1);
          this.$router.push('/');
        }, 1500);
      } else if (
        this.rulerFrom.username == 'xunuo' &&
        this.rulerFrom.password == '123456'
      ) {
        this.$message({
          message: '尊敬的鬼斯通',
          type: 'success'
        });
        setTimeout(() => {
          localStorage.setItem('token', '登入');
          localStorage.setItem('rule', 2);
          this.$router.push('/');
        }, 1500);
      } else {
        this.$message({
          message: '账户不正确',
          type: 'warning'
        });
      }
    },
    messages() {
      let token = localStorage.getItem('token');
      if (token != '') {
        this.$message({
          message: 'token已过期或登出',
          type: 'warning'
        });
      }
    }
  },
  mounted() {
    this.messages();
  }
};
</script>
